import { Injectable } from '@angular/core';
import {Http, RequestOptions} from '@angular/http';
import {Observable} from 'rxjs/Observable';
import {Headers} from '@angular/http';
import {isNullOrUndefined} from 'util';

@Injectable()
export class AuthenticationService {

  constructor(private http: Http) { }

  authenticate (value: any): Observable<any> {
    const bodyString = JSON.stringify(value.userId);
    const headers      = new Headers({ 'Content-Type': 'application/json', 'cache-control': 'no-cache' });
    const options       = new RequestOptions({ headers: headers });
    value.password = undefined;
    return this.http.post('http://localhost:8080/login', value.userId, options);
  }

  register(value: any): Observable<any> {
    const headers      = new Headers({ 'Content-Type': 'application/json', 'cache-control': 'no-cache' });
    const options       = new RequestOptions({ headers: headers });
    return this.http.post('http://localhost:8080/register', value, options);
  }
}



